import { Component, Input, forwardRef } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR, ReactiveFormsModule } from '@angular/forms';
import { FloatLabelModule } from 'primeng/floatlabel';
import { InputText } from 'primeng/inputtext';

@Component({
  selector: 'pla-input-text-frm',
  imports: [InputText, FloatLabelModule, ReactiveFormsModule],
  templateUrl: './pla-input-text-frm.component.html',
  styleUrl: './pla-input-text-frm.component.css',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => PlaInputTextFrm),
      multi: true
    }
  ]
})
export class PlaInputTextFrm implements ControlValueAccessor {
  @Input() label: string = '';
  @Input() type: string = 'text';

  value: any = '';
  onChange: any = () => { };
  onTouched: any = () => { };

  writeValue(value: any): void {
    this.value = value;
  }

  registerOnChange(fn: any): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: any): void {
    this.onTouched = fn;
  }

  setDisabledState?(isDisabled: boolean): void {
    // Implement logic to handle disabled state if needed
  }

  onInputChange(event: any): void {
    this.value = event.target.value;
    this.onChange(this.value);
    this.onTouched();
  }
}
